<template>
  <div class="box" id="box">
    <div class="d_imgBox" id="item" v-for="item in items">
      <img v-bind:src="item.src">
      <div class="cover">
        <div class="time">{{item.time}}</div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: "three",
  data() {
    return {
      items: [
        { src: "../../static/y1.png", time: "2017-4-4" },

        { src: "../../static/design/ji.png", time: "2019-1-31" },
        { src: "../../static/design/d1.gif", time: "2018-4-4" },
        { src: "../../static/design/d3.png", time: "2018-4-4" },
        { src: "../../static/design/san.png", time: "2019-4-10" },

        { src: "../../static/design/baby2.png", time: "2019-1-31" },

        { src: "../../static/design/zz.png", time: "2019-1-31" },
        { src: "../../static/design/d2.gif", time: "2018-4-4" },

        { src: "../../static/design/2.png", time: "2019-1-31" }
      ]
    };
  },
  methods: {},
  mounted() {
    var box = document.getElementById("box");
    var items = box.children;
    //  10
    var gap = 20;

    // 
    // this.$nextTick(() => {
    //   items[8].onload = function() {
    waterFall();
    //   };
    // });

    // 
    function waterFall() {
      // 1-   =  / 
      var pageWidth = getClient().width * 0.75;
      var itemWidth = items[0].offsetWidth;
      var columns = parseInt(pageWidth / (itemWidth + gap));
      var arr = [];
      for (var i = 0; i < items.length; i++) {
        if (i < columns) {
          // 2- 

          items[i].style.top = 0;
          items[i].style.left = (itemWidth + gap) * i + "px";
          arr.push(items[i].offsetHeight);
        } else {
          // 
          // 3-    
          var minHeight = arr[0];
          var index = 0;
          for (var j = 0; j < arr.length; j++) {
            if (minHeight > arr[j]) {
              minHeight = arr[j];
              index = j;
            }
          }
          // 4- 
          // top + gap
          items[i].style.top = arr[index] + gap + "px";
          // left
          items[i].style.left = items[index].offsetLeft + "px";

          // 5- 
          //  =  +  + 
          arr[index] = arr[index] + items[i].offsetHeight + gap;
        }
      }
    }
    // 
    window.onresize = function() {
      waterFall();
    };
    // 30

    // clientWidth 
    function getClient() {
      return {
        width:
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth,
        height:
          window.innerHeight ||
          document.documentElement.clientHeight ||
          document.body.clientHeight
      };
    }
    // scrollTop
    function getScrollTop() {
      return window.pageYOffset || document.documentElement.scrollTop;
    }
  }
};
</script>  


<style scoped>
.box {
  width: 1160px;
  height: 100%;
}
.d_imgBox {
  /*
    width:22%;
    margin-right:2.5%;
    overflow:hidden;
    float:left;
    margin-bottom:20px;*/

  /*box-shadow: 1px 1px 1px #459ad6;*/
  position: absolute;
}
.d_imgBox img {
  /*width:100%;
    min-width:256px;*/

  width: 290px;
  display: block;
  animation-play-state: paused;
}

.cover {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  bottom: 0px;
  z-index: 2;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0), #f8d4cb);
  opacity: 0.6;
  transition: all 0.4s;
  pointer-events: none;
}
#item:hover .cover {
  opacity: 0;
}
.cover .time {
  color: #fff;
  position: absolute;
  right: 40%;
  bottom: 15px;
  font-size: 14px;
  z-index: 3;
}
</style>


